.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 20px;
	flex-wrap: wrap;
}

.movie-wrapper {
	width: 500px;
	background-color: antiquewhite;
	color: black;
}

.movie-search {
	height: 48px;
	font-size: 24px;
	margin-bottom: 32px;
	padding: 16px;
	font-weight: bold;
	width: 475px;
}

.movie-title {
	height: 60px;
	margin-top: 26px;
	font-weight: bold;
}

.movie-date {
	font-style: italic;
	margin-top: 26px;
}

.movie-rate {
	color: red;
	margin-bottom: 24px;
	font-weight: bold;
	font-size: 1.2em;
	text-decoration: underline;
}